<template>
  <b-field>
    <b-select
      placeholder='Pick language'
      v-model='choosed'
      size='is-large'
      expanded
    >
      <option v-for='language in languages' v-bind:value='language.slug'>
        {{language.name}}
      </option>
    </b-select>
  </b-field>
</template>

<script>
export default {
  name: 'language-picker',
  props: {
    language: String,
    languages: Array,
  },
  data() {
    return {
      choosed: '',
    };
  },
  created() {
    this.choosed = this.value;
  },
  watch: {
    choosed() {
      this.$emit('input', this.choosed);
    },
  },
};
</script>

<style>
select {
  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1),0 0 0 1px rgba(10, 10, 10, 0.1) !important;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1) !important;
}
</style>



