<template>
  <div class="card">
    <div class="card-content">
      <div class="columns is-multiline is-mobile">
        <word-item
          v-for="word in words"
          v-bind:key="word.name"
          :name="word.name"
          :emoji="word.emoji"
          :translation="getTranslation(word.translations)"
        ></word-item>
      </div>
    </div>
  </div>
</template>

<script>
import WordItem from '@/components/WordItem';

export default {
  name: 'word-category',
  props: {
    name: String,
    words: Array,
    language: String,
  },
  components: {
    WordItem,
  },
  methods: {
    getTranslation(translations) {
      const language = this.language;
      return Object.prototype.hasOwnProperty.call(translations, language) ? translations[language] : 'translation not found :(';
    },
  },
};
</script>

<style scoped>
.card {
  margin-top: 2rem;
  border-top: 3px solid #7957d5;;
}
</style>


