<template>
  <main>
    <language-picker
      v-model="language"
      :languages="languages"
    ></language-picker>

    <word-info
      :wordCount="wordCount"
      :translatedWordCount="translatedWordCount"
    ></word-info>

      <b-tabs
        v-model="activeTab"
        :expanded="true"
      >
        <b-tab-item label="all">
          <word-category
            v-for="category in categories"
            v-bind:key="category.name"
            :name="category.name"
            :words="category.words"
            :language="language"
          ></word-category>
        </b-tab-item>

        <b-tab-item
          v-for="category in categories"
          :label="category.name"
        >
          <word-category
            v-bind:key="category.name"
            :name="category.name"
            :words="category.words"
            :language="language"
          ></word-category>
        </b-tab-item>
      </b-tabs>
  </main>
</template>

<script>
import languageData from '@/data/languages.json';
import wordsData from '@/data/words.json';
import LanguagePicker from '@/components/LanguagePicker';
import WordInfo from '@/components/WordInfo';
import WordCategory from '@/components/WordCategory';

export default {
  name: 'app-main',
  components: {
    LanguagePicker,
    WordInfo,
    WordCategory,
  },
  data() {
    return {
      language: '',
      activeTab: 0,
    };
  },
  computed: {
    languages() {
      return Object.entries(languageData).map((language) => {
        const [slug, { name, nativeName }] = language;
        return {
          slug,
          name,
          nativeName,
        };
      });
    },
    categories() {
      const all = [];
      wordsData.forEach((word) => {
        const wordCategory = word.category;
        const category = all.find(({ name }) => name === wordCategory);
        if (!category) {
          all.push({
            name: wordCategory,
            words: [word],
          });
        } else {
          category.words.push(word);
        }
      });
      return all;
    },
    wordCount() {
      /* eslint-disable no-param-reassign */
      /* eslint-disable no-unused-vars */
      /* eslint-disable no-return-assign */
      return wordsData.reduce((count, word) => count += 1, 0);
    },
    translatedWordCount() {
      const choosedLanguage = this.language;
      return wordsData.reduce((total, { translations }) => {
        if (translations[choosedLanguage]) {
          return total += 1;
        }
        return total;
      }, 0);
    },
  },
};
</script>

<style>
.b-tabs {
  margin-top: 2rem;
}

.tab-content {
  padding: 0.5rem;
}
</style>



